<script>
  // Import statements
import ModProfessional from "../routes/ModProfessional.svelte"

import {
  currentUser,
  currentProfile,
  allUsers,
  allProfs,
  allSuperProfs,
  allAdmins,
  allProgs,
  profProgs,
  altProfile,
  //profProfile,
  addingStatus,
  filteredUserArray,
  filteredProfArray,
  filteredAdminArray,
  filteredSuperProfArray,
  filteredProgArray,
  filteredProfProgArray,
  loadingCircle
} from '../stores.js';

import AddUser from '../routes/AddUser.svelte';
import { fetchUsers, fetchProfs, fetchProgs, getAltProfile, getProfProfile, fetchProfProgs, fetchSuperProfs, fetchAdmins } from '../fileDbHandling.js';
import { signUp } from '../loginService.js';
import {
login,
pwd,
name,
surname,
phone,
email,
role,
gender,
dob,
address,
pocode
} from '../stores.js';

import { deleteFile } from "../file-handler";




// Variables and stores

let toggle = false;
let profToggle = false;
let userArray = [];
let ProfArray = [];
let ProgArray = [];
let superProfArray = [];
let AdminArray =[];
let ProfProgArray =[];
let activeTab = 'users';
let activeProgramTab = 'users';
let activeWindow = 'users';
let searchTerm = "";
let profile = {
  name: " ",
}; 


let currentUserProgramPage = 1;
let currentProfProgramPage = 1;
let userProgramCount = 0;
let profProgramCount = 0;
const entriesPerPage = 100;
let startUserProgramIdx,enduserProgramIdx, startProfProgramIdx,endProfProgramIdx;
$: startUserProgramIdx = (currentUserProgramPage-1) * entriesPerPage;
$: enduserProgramIdx = startUserProgramIdx + entriesPerPage;
$: userProgramPageCount = Math.ceil(userProgramCount/entriesPerPage);
$: startProfProgramIdx = (currentProfProgramPage-1) * entriesPerPage;
$: endProfProgramIdx = startProfProgramIdx + entriesPerPage;
$: profProgramPageCount = Math.ceil(profProgramCount/entriesPerPage);

fresh();
// Functions

async function getProfs() {
  
  const unsubscribeProfs = allProfs.subscribe(profs => {
      ProfArray = [...profs];
      filteredProfArray.set(ProfArray);
    });
    allProfs.set([]);
  await getAllProfs();
  unsubscribeProfs();
  //console.log(ProfArray);

}

async function getProgs() {
  
  const unsubscribeProgs = allProgs.subscribe(progs => {
      userProgramCount = progs.length;
      ProgArray = [...progs];
      filteredProgArray.set(ProgArray);
    });
    allProgs.set([]);
  await getAllProgs();
  unsubscribeProgs();
  //console.log(ProfArray);

}



async function getSuperProfs() {
  
  const unsubscribeSuperProfs = allSuperProfs.subscribe(profs => {
      superProfArray = [...profs];
      filteredSuperProfArray.set(superProfArray);
    });
    filteredSuperProfArray.set([]);
  await getAllSuperProfs();
  unsubscribeSuperProfs();
  //console.log(ProfArray);

}

async function getAdmins() {
  
  const unsubscribeAdmins = allAdmins.subscribe(profs => {
      AdminArray = [...profs];
      filteredAdminArray.set(AdminArray);
    });
    allAdmins.set([]);
  await getAllAdmins();
  unsubscribeAdmins();
  //console.log(ProfArray);

}

async function getProfProgs() {
  const unsubscribeProfProgs = profProgs.subscribe(profProgs =>{
    profProgramCount = profProgs.length;
    ProfProgArray = [...profProgs];
    filteredProfProgArray.set(ProfProgArray);
  });
  await fetchProfProgs();
  unsubscribeProfProgs();
  
}

async function getUsers() {
  const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = [...users];
      filteredUserArray.set(userArray);
    });
    allUsers.set([]);
  await getAllUsers();
  
 
  unsubscribeUsers();
}

function getProf(id) {
  profProfile.set(findProfById(id));
  toggleProf();
}

function findProfById(id) {
  return ProfArray.find(prof => prof.id === id) || null;
}

function findUserById(id) {
  return userArray.find(user => user.id === id) || null;
}
function findProfessionalById(id) {
  return ProfArray.find(user => user.id === id) || null;
}


async function getAllUsers() {
  await fetchUsers();
  //console.log("Fetched all users");
}

async function getAllProfs() {
  await fetchProfs();
  //console.log("Fetched all professionals");
}
async function getAllProgs() {
  await fetchProgs();
  //console.log("Fetched all professionals");
}


async function getAllSuperProfs() {
  await fetchSuperProfs();
  //console.log("Fetched all professionals");
}

async function getAllAdmins() {
  await fetchAdmins();
  //console.log("Fetched all professionals");
}


function openUser(id) {
  console.log(id);
  addingStatus.set(2);
  altProfile.set(findUserById(id));
}

function openProfessional(id) {
  console.log("opening prof");
  //addingStatus.set(3);
  altProfile.set(findProfessionalById(id));
  console.log(findProfessionalById(id));
  activeWindow = "modProfessional";
}

function switchTab(tab) {
  if(activeWindow == 'users'){
  activeTab = tab;
  }else{
    currentUserProgramPage = 1;
    currentProfProgramPage = 1;
    activeProgramTab = tab;
  }
}
function switchWindow(window) {
  activeWindow = window;
  handleSearch({target: {value: ''}});
  //console.log(window);
}

async function refresh(){
    
    
    loadingCircle.set(true);
      if(activeWindow == 'users'){
        if(activeTab == 'users'){            
            await getUsers();
        }else if(activeTab == 'professionals'){
          await getProfs();
        }else if(activeTab == 'superProfessionals'){
          await getSuperProfs();
        }else if(activeTab == 'admins'){
          await getAdmins();
        }

        
      }else if(activeWindow == 'programs'){
        await getProgs();
        await getProfProgs();
        
      }
      
     loadingCircle.set(false);
      

    

}
function delay(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}
async function fresh(){
    
    
    await getUsers();
    await getProfs();
    await getProgs();
    await getSuperProfs();
    await getAdmins();
    await getProfProgs();
    
}




function handleSearch(event) {
  
  searchTerm = event.target.value.toLowerCase();
  //console.log(searchTerm);
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) ||
        user.email.toLowerCase().includes(searchTerm) ||
        user.phone_number.toLowerCase().includes(searchTerm) ||
        user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredProfArray.set(
    ProfArray.filter(professional => {
      return (
        professional.name.toLowerCase().includes(searchTerm) ||
        professional.surname.toLowerCase().includes(searchTerm) ||
        professional.email.toLowerCase().includes(searchTerm) ||
        professional.phone_number.toLowerCase().includes(searchTerm) ||
        professional.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredSuperProfArray.set(
    superProfArray.filter(professional => {
      return (
        professional.name.toLowerCase().includes(searchTerm) ||
        professional.surname.toLowerCase().includes(searchTerm) ||
        professional.email.toLowerCase().includes(searchTerm) ||
        professional.phone_number.toLowerCase().includes(searchTerm) ||
        professional.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredAdminArray.set(
    AdminArray.filter(admin => {
      return (
        admin.name.toLowerCase().includes(searchTerm) ||
        admin.surname.toLowerCase().includes(searchTerm) ||
        admin.email.toLowerCase().includes(searchTerm)
      );
    })
  );

  let filteredTempProgArray = ProgArray.filter(program => {
      return (
        program.name.toLowerCase().includes(searchTerm) ||
        program.user_surname.toLowerCase().includes(searchTerm) ||
        program.user_name.toLowerCase().includes(searchTerm) ||
        program.folder_name.toLowerCase().includes(searchTerm)
      );
    });
  userProgramCount = filteredTempProgArray.length;
  filteredProgArray.set(
    filteredTempProgArray
  );
  currentUserProgramPage = 1;

   let filteredTempProfProgArray = ProfProgArray.filter(program => {
      return (
        program.name.toLowerCase().includes(searchTerm) ||
        program.professional_surname.toLowerCase().includes(searchTerm) ||
        program.professional_name.toLowerCase().includes(searchTerm)
      );
    });
  profProgramCount = filteredTempProfProgArray.length;
  filteredProgArray.set(
    filteredTempProgArray
  );
  currentUserProgramPage = 1;


}

// Main component


$: {
  // Update filteredUserArray when searchTerm or userArray changes
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) //||
        //user.email.toLowerCase().includes(searchTerm) ||
        //user.phone_number.toLowerCase().includes(searchTerm) ||
        //user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );
}


$: {
  // Update filteredUserArray when searchTerm or userArray changes
  filteredProfArray.set(
    ProfArray.filter(professional => {
      return (
        professional.name.toLowerCase().includes(searchTerm) ||
        professional.surname.toLowerCase().includes(searchTerm) //||
        //user.email.toLowerCase().includes(searchTerm) ||
        //user.phone_number.toLowerCase().includes(searchTerm) ||
        //user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );
}
function nextPage() {
    if(activeProgramTab == 'users'){
      currentUserProgramPage += 1;
    }else  if(activeProgramTab == 'professionals'){
      currentProfProgramPage += 1;
    } 
  }

  // Create a function to handle navigation to the previous section
  function previousPage() {
    if(activeProgramTab == 'users'){
      currentUserProgramPage -= 1;
    }else  if(activeProgramTab == 'professionals'){
      currentProfProgramPage -= 1;
    } 
  }

  function goToPage(page) {
    if(activeProgramTab == 'users'){
      currentUserProgramPage = page;
    }else  if(activeProgramTab == 'professionals'){
      currentProfProgramPage = page;
    } 
  }


async function onSignUp() {
        // Sign up functionality
        await signUp();
        
    }

    function closeForm(){
      const popup = document.getElementById('addUserPopup');
      //console.log("opening popup 1");
      popup.style.display = 'none';
    }
    function openForm(){
      const popup = document.getElementById('addUserPopup');
      //console.log("opening popup 1");
      popup.style.display = 'block';
    }

async function deleteProgram(file_id, role) {
  loadingCircle.set(true);
    
  try {
    
    
    //console.log(role);
    await deleteFile(file_id, role);
    await fetchUsers();
    await loadingCircle.set(false);
    //console.log("set false");

    // Promise resolved successfully, show success alert
    
    refresh();
    
    
  } catch (error) {
    loadingCircle.set(false);
    alert("Could not delete");
    console.log(error);
  }
   
}



</script>

<main>
  <div class = "super-container">
    <div class = "side-bar">
      <div class="user-details">
          
         
      </div>
      <div class="side-bar-buttons">
        <button class="{activeWindow === 'profile' ? 'active' : ''}" on:click={() => switchWindow('profile')}>
          <i class="fa-solid fa-id-badge"></i>
            <p>Details</p>
        </button>
        <button class="{activeWindow === 'users' ? 'active' : ''}" on:click={() => switchWindow('users')}>
          <i class="fa-solid fa-users"></i>
          <p>Users</p>
        </button>
        <button class="{activeWindow === 'programs' ? 'active' : ''}" on:click={() => switchWindow('programs')}>
          <i class="fa-solid fa-file-waveform"></i>
          <p>Programs</p>
        </button>
      </div>
      
           
    </div>

      <div class="lower-container">
        
          
        {#if activeWindow === 'profile'}
            
      <div class = "profile-window">
        <div class="user-details">
          <div class="user-info">
            <h3 class="profilename">{$currentProfile.name} {$currentProfile.surname}</h3>
            <p class="profile-role">{$currentUser.user_role}</p>
            <div class="card-content">
              <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-envelope"></i></div>
                  <div class="info-value">{$currentProfile.email}</div>
              </div>
              <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-cake-candles"></i></div>
                  <div class="info-value">{$currentProfile.date_of_birth}</div>
              </div>
              <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-phone"></i></div>
                  <div class="info-value">{$currentProfile.phone_number}</div>
              </div>
              <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-person-half-dress"></i></div>
                  <div class="info-value">{$currentProfile.gender}</div>
              </div>
              
            <div class="info-row">
              <div class="info-label"><i class="fa-solid fa-location-dot"></i></div>
              <div class="info-value">{$currentProfile.address}</div>
          </div>
          </div>
          </div>
          <div class = "avatar-div">
            {#if $currentUser.user_role === "Administrator"}
              <i class="fa-solid fa-user-shield fa-2xl"></i>
            {:else if $currentUser.user_role === "Professional" || $currentUser.user_role === "S. Professional"}
              <i class="fa-solid fa-user-tie fa-2xl"></i>
            {:else}
              <i class="fa-solid fa-user fa-2xl"></i>
            {/if}
          </div>
        </div>
            
            
        </div>
        
    
        {/if}
          
          {#if activeWindow === 'users'}
          <div class="action-bar">
            <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
            <button on:click={openForm} class="add-user-button">
              <i class="fas fa-user-plus"></i>
            </button>
            <button on:click={refresh} class="add-user-button">
              <i class="fas fa-rotate"></i>
            </button>
          </div>
          <div class="tab-selector">
              <button class="{activeTab === 'users' ? 'active' : ''}" on:click={() => switchTab('users')}>Users</button>
              <button class="{activeTab === 'professionals' ? 'active' : ''}" on:click={() => switchTab('professionals')}>Professionals</button>
              <button class="{activeTab === 'superProfessionals' ? 'active' : ''}" on:click={() => switchTab('superProfessionals')}>S. Professionals</button>
              <button class="{activeTab === 'admins' ? 'active' : ''}" on:click={() => switchTab('admins')}>Admins</button>          
          </div>

          {#if activeTab === 'users'}
          <div class="user-list-container">
            <div class="file-list">
              
              <table class="user-table">
                <!-- Table header -->
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Professional</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
          
                <!-- Table body -->
                <tbody>
                  {#each $filteredUserArray as user}
                  <tr class="clickable-row" on:click={() => openUser(user.id)}>
                    <td class="table-entry">{user.name} {user.surname}</td>
                    <td class="table-entry">{user.email}</td>
                    <td class="table-entry">{user.phone_number}</td>
                    <td class="table-entry">{user.professional}</td>
                    <td class="table-entry">{user.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          {/if}

          {#if activeTab === 'professionals'}
          <div class="user-list-container">
            <div class="file-list">
              <table class="user-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredProfArray as professional}
                  <tr class="clickable-row" on:click={openProfessional(professional.id)}>
                    <td class="table-entry">{professional.name} {professional.surname}</td>
                    <td class="table-entry">{professional.email}</td>
                    <td class="table-entry">{professional.phone_number}</td>
                    <td class="table-entry">{professional.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          
          {/if}
          {#if activeTab === 'superProfessionals'}
          <div class="user-list-container">
            <div class="file-list">
              <table class="user-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredSuperProfArray as professional}
                  <tr class="clickable-row" on:click={openProfessional(professional.id)}>
                    <td class="table-entry">{professional.name} {professional.surname}</td>
                    <td class="table-entry">{professional.email}</td>
                    <td class="table-entry">{professional.phone_number}</td>
                    <td class="table-entry">{professional.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          
          {/if}
          {#if activeTab === 'admins'}
          <div class="user-list-container">
            <div class="file-list">
              <table class="user-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredAdminArray as admin}
                  <tr>
                    <td class="table-entry">{admin.name} {admin.surname}</td>
                    <td class="table-entry">{admin.email}</td>
                    <td class="table-entry">{admin.phone_number}</td>
                    <td class="table-entry">{admin.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          
          {/if}
          {/if}

          
          {#if activeWindow === 'programs'}
          <div class="action-bar">
            <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
            <button on:click={refresh} class="add-user-button">
              <i class="fas fa-rotate"></i>
            </button>
          </div>
          <div class="tab-selector">
            <button class="{activeProgramTab === 'users' ? 'active' : ''} program-tab" on:click={() => switchTab('users')}>Users</button>
            <button class="{activeProgramTab === 'professionals' ? 'active' : ''} program-tab" on:click={() => switchTab('professionals')}>Professionals</button>          
          </div>
          
          {#if activeProgramTab == 'users'}
            <div class="user-list-container">
              <div class="file-list">
                    <table class="program-table">
                      <thead class="sticky-header">
                        <tr>
                          <th class="table-heading">File</th>
                          <th class="table-heading">Folder</th>
                          <th class="table-heading">User</th>
                          <th class="table-heading">Created</th>
                          <th class="table-heading"> </th>
                        </tr>
                      </thead>
                      <tbody>
                        {#each $filteredProgArray.slice(startUserProgramIdx, enduserProgramIdx) as program}
                        <!-- Display the limited entries based on the current page -->
                        <tr>
                          <td class="table-entry">{program.name}</td>
                          <td class="table-entry">{program.folder_name}</td>
                          <td class="table-entry">{program.user_name} {program.user_surname}</td>
                          <td class="table-entry">{program.created}</td>
                          <td class="table-entry"><button on:click={() => deleteProgram(program.id, "ROLE_USER")}><i class="fa-solid fa-trash delete-button"></i></button></td>
                        </tr>
                        {/each}
                      </tbody>
                    </table>
                </div>
              </div>
            <div>
            <p><b>Page:</b> {currentUserProgramPage} of {userProgramPageCount} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <b>Programs:</b> {startUserProgramIdx+1} to {enduserProgramIdx} of {userProgramCount}</p>
            </div>
            <div>
              <button class="nav-button" on:click={() => goToPage(1)} disabled={currentUserProgramPage === 1}><i class="fa-solid fa-backward-fast"></i></button>
              <button class="nav-button" on:click={previousPage} disabled={currentUserProgramPage === 1}><i class="fa-solid fa-backward-step"></i></button>

                <!-- Page Navigation Buttons -->
                {#if userProgramPageCount <= 5}
                  <!-- Show all page buttons -->
                  {#each Array.from({ length: userProgramPageCount }, (_, i) => i + 1) as page}
                    <button
                      on:click={() => goToPage(page)}
                      class="nav-button"
                      class:selected={currentUserProgramPage === page}
                    >
                      {page}
                    </button>
                  {/each}
                {:else}
                  <!-- Show range of page buttons with current page centered -->
                  {#if currentUserProgramPage <= 3}
                    {#each Array.from({ length: Math.min(5, userProgramPageCount) }, (_, i) => i + 1) as page}
                      <button
                        on:click={() => goToPage(page)}
                        class="nav-button"
                        class:selected={currentUserProgramPage === page}
                      >
                        {page}
                      </button>
                    {/each}
                  {:else if currentUserProgramPage >= userProgramPageCount - 2}
                    {#each Array.from({ length: Math.min(5, userProgramPageCount) }, (_, i) => userProgramPageCount - 4 + i) as page}
                      <button
                        on:click={() => goToPage(page)}
                        class="nav-button"
                        class:selected={currentUserProgramPage === page}
                      >
                        {page}
                      </button>
                    {/each}
                  {:else}
                    {#each Array.from({ length: 5 }, (_, i) => currentUserProgramPage - 2 + i) as page}
                      <button
                        on:click={() => goToPage(page)}
                        class="nav-button"
                        class:selected={currentUserProgramPage === page}
                      >
                        {page}
                      </button>
                    {/each}
                  {/if}
                {/if}
                <!-- Next Page Button -->
                <button class="nav-button" on:click={nextPage} disabled={currentUserProgramPage >= userProgramPageCount}><i class="fa-solid fa-forward-step"></i></button>
                <button class="nav-button" on:click={() => goToPage(userProgramPageCount)} disabled={currentUserProgramPage >= userProgramPageCount}><i class="fa-solid fa-forward-fast"></i></button>
            </div>
            {:else if activeProgramTab == 'professionals'}
                <div class="user-list-container">
                  <div class="file-list">
                        <table class="program-table">
                          <thead class="sticky-header">
                            <tr>
                              <th class="table-heading">File</th>
                              <th class="table-heading">Professional</th>
                              <th class="table-heading">Created</th>
                              <th class="table-heading"> </th>
                            </tr>
                          </thead>
                          <tbody>
                            {#each $filteredProfProgArray.slice(startUserProgramIdx, enduserProgramIdx) as program}
                            <!-- Display the limited entries based on the current page -->
                            <tr>
                              <td class="table-entry">{program.name}</td>
                              <td class="table-entry">{program.professional_name} {program.professional_surname}</td>
                              <td class="table-entry">{program.created_at}</td>
                              <td class="table-entry"><button on:click={() => deleteProgram(program.id, "ROLE_PROFESSIONAL")}><i class="fa-solid fa-trash delete-button"></i></button></td>
                            </tr>
                            {/each}
                          </tbody>
                        </table>
                    </div>
                  </div>
                <div>
                <p><b>Page:</b> {currentProfProgramPage} of {profProgramPageCount} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <b>Programs:</b> {startProfProgramIdx+1} to {endProfProgramIdx} of {profProgramCount}</p>
                </div>
                <div>
                  <button class="nav-button" on:click={() => goToPage(1)} disabled={currentProfProgramPage === 1}><i class="fa-solid fa-backward-fast"></i></button>
                  <button class="nav-button" on:click={previousPage} disabled={currentProfProgramPage === 1}><i class="fa-solid fa-backward-step"></i></button>

                    <!-- Page Navigation Buttons -->
                    {#if profProgramPageCount <= 5}
                      <!-- Show all page buttons -->
                      {#each Array.from({ length: profProgramPageCount }, (_, i) => i + 1) as page}
                        <button
                          on:click={() => goToPage(page)}
                          class="nav-button"
                          class:selected={currentProfProgramPage === page}
                        >
                          {page}
                        </button>
                      {/each}
                    {:else}
                      <!-- Show range of page buttons with current page centered -->
                      {#if currentProfProgramPage <= 3}
                        {#each Array.from({ length: Math.min(5, profProgramPageCount) }, (_, i) => i + 1) as page}
                          <button
                            on:click={() => goToPage(page)}
                            class="nav-button"
                            class:selected={currentProfProgramPage === page}
                          >
                            {page}
                          </button>
                        {/each}
                      {:else if currentProfProgramPage >= profProgramPageCount - 2}
                        {#each Array.from({ length: Math.min(5, profProgramPageCount) }, (_, i) => profProgramPageCount - 4 + i) as page}
                          <button
                            on:click={() => goToPage(page)}
                            class="nav-button"
                            class:selected={currentProfProgramPage === page}
                          >
                            {page}
                          </button>
                        {/each}
                      {:else}
                        {#each Array.from({ length: 5 }, (_, i) => currentProfProgramPage - 2 + i) as page}
                          <button
                            on:click={() => goToPage(page)}
                            class="nav-button"
                            class:selected={currentProfProgramPage === page}
                          >
                            {page}
                          </button>
                        {/each}
                      {/if}
                    {/if}
                    <!-- Next Page Button -->
                    <button class="nav-button" on:click={nextPage} disabled={currentProfProgramPage >= profProgramPageCount}><i class="fa-solid fa-forward-step"></i></button>
                    <button class="nav-button" on:click={() => goToPage(profProgramPageCount)} disabled={currentProfProgramPage >= profProgramPageCount}><i class="fa-solid fa-forward-fast"></i></button>
                </div>
                
            {/if}
    
          
          
         
          {/if}
          {#if activeWindow == "modProfessional"}
              <ModProfessional />
          {/if}
          
      </div>

      
  </div>

  <div id="addUserPopup" class="popup">    
      <div class="form-container">
        <h1>Add User/Professional</h1>
        <form on:submit|preventDefault={onSignUp}>
            <div class="form-columns">
                <div class="form-column">
                    <label>
                        Username:
                        <input type="text" bind:value={$login} required />
                    </label>
                    <label>
                        Password:
                        <input type="password" bind:value={$pwd} required />
                    </label>
                    <label>
                        Name:
                        <input type="text" bind:value={$name} required />
                    </label>
                    <label>
                        Surname:
                        <input type="text" bind:value={$surname} required />
                    </label>
                    <label>
                        Role:
                        <select bind:value={$role} required>
                            <option value="">Select Role</option>
                            <option value="ROLE_PROFESSIONAL">Professional</option>
                            <option value="ROLE_USER">User</option>
                        </select>
                    </label>
                </div>
                <div class="form-column">
                    <label>
                        Gender:
                        <select bind:value={$gender} required>
                            <option value="">Select Gender</option>
                            <option value="male">Male</option>
                            <option value="female">Female</option>
                        </select>
                    </label>
                    <label>
                        Date of Birth:
                        <input type="date" bind:value={$dob} required />
                    </label>
                    <label>
                        Email:
                        <input type="email" bind:value={$email} required />
                    </label>
                    <label>
                        Phone Number:
                        <input type="tel" bind:value={$phone} required />
                    </label>
                    <label>
                        Address:
                        <input type="text" bind:value={$address} required />
                    </label>
                    <label>
                        Postal Code:
                        <input type="number" bind:value={$pocode} required />
                    </label>
                </div>
            </div>
            <button type="submit">Add</button>
        </form>
        <button on:click={closeForm}>Cancel</button>
    </div>
  </div>

 

   
</main>
   
        

      
 
  
