export const uniqueProgs = derived(allProgs, $allProgs => {
	const uniqueNames = new Set();
	const result = [];

	for (const obj of $allProgs) {
		if (!uniqueNames.has(obj.name)) {
		uniqueNames.add(obj.name);
		result.push(obj);
		}
	}

	return result;
});



