<script>
    import { fade, slide } from 'svelte/transition';
    import { addingStatus, altProfile, files, programs, filteredUserArray, filteredProgArray, allUsers, profProgs} from "../stores";
    import { fetchFolders } from '../fileDbHandling.js';
    import { fetchPrograms } from '../fileDbHandling.js';
    import { fetchProfProgs } from "../fileDbHandling.js";
    import { updateProfessional } from "../fileDbHandling.js";
    import { fetchUsers } from "../fileDbHandling.js";
    import{uploadFile} from "../file-handler.js";
    import { loadingCircle } from '../stores';
    import { deleteFile } from '../file-handler.js';
    import { uniqueProgs } from '../stores';
  
    

    let toggle = false;
    let searchTerm = "";
    let popupSearchTerm = "";
    let currentProfile;
    let folders = [];
    let programArray = [];
    let isProgramList = false;
    let user_id = 3;
    let activeTab = 'users';
    let userArray = [];
    let ProgArray = [];
    
    let selectedUser;
    let selectedProgram = {id: 0};

    fetchFileHistory();






    const unsubscribeId = altProfile.subscribe(aId => {
        currentProfile = aId;
        user_id = aId.user_id; 
        //console.log(currentProfile);   
    });

    unsubscribeId();
    const unsubscribeProgs = profProgs.subscribe(progs => {
      ProgArray = progs.filter(prog => prog.professional_id === user_id);
      filteredProgArray.set(ProgArray);
    });
    unsubscribeProgs();

    const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = users.filter(user => user.professional_id === user_id);
      filteredUserArray.set(userArray);
    });
    unsubscribeUsers();




   
    function toggleDetails (){
      toggle = !toggle;
  }

  function handleSearch(event) {
  
  searchTerm = event.target.value.toLowerCase();
  //console.log(searchTerm);
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) ||
        user.email.toLowerCase().includes(searchTerm) ||
        user.phone_number.toLowerCase().includes(searchTerm) ||
        user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredProgArray.set(
    ProgArray.filter(program => {
      return (
        program.name.toLowerCase().includes(searchTerm)
                
      );
    })
  );
}
function switchTab(tab) {
  activeTab = tab;
}
function openUser(id) {
  console.log(id);
  addingStatus.set(2);
  altProfile.set(findUserById(id));
}

function openProfessional(id) {
  console.log("opening prof");
  addingStatus.set(3);
  altProfile.set(findProgramById(id));
}

function findUserById(id) {
  return userArray.find(user => user.id === id) || null;
}
function findProgramById(id) {
  return ProgArray.find(user => user.id === id) || null;
}

function openPopup(n) {
  console.log("opening popups");
  if( n == 1)
  {
    const popup = document.getElementById('uploadPopup');
    //console.log("opening popup 1");
    popup.style.display = 'block';
    const userIdInput = document.getElementById('userIdInput');
    userIdInput.value = currentProfile.user_id;
  }else if(n == 2) {
    const popup = document.getElementById('addProfPopup');
    //console.log("opening popup 2");
    popup.style.display = 'block';
  }else if(n == 3){
    const popup = document.getElementById('addFilePopup');
    //console.log("opening popup 2");
    popup.style.display = 'block';
  }
  
  //console.log(currentProfile);
}

export function closePopup(n) {
  if(n == 1){
    const popup = document.getElementById('uploadPopup');
    popup.style.display = 'none';
  }else if(n == 2){
    const popup = document.getElementById('addProfPopup');
    popup.style.display = 'none';
  }else if(n == 3){
    const popup = document.getElementById('addFilePopup');
    //console.log("opening popup 2");
    popup.style.display = 'none';
  }
  
}

async function uploadFileFromPopup() {
  const fileInput = document.getElementById('fileInput');
  const userIdInput = document.getElementById('userIdInput');
  const updateCheckbox = document.getElementById('updateCheckbox');

  const file = fileInput.files[0];
  const userId = userIdInput.value;
  const update = updateCheckbox.checked;

  if (file && userId) {
    loadingCircle.set(true);
    await uploadFile(file, userId, update);
    await fetchFileHistory();
    closePopup(1);
    
  } else {
    console.error('Please select a file and provide a user ID.');
  }
}

function addFileFromPopup(){
  console.log(selectedProgram);
}
async function updateProfessionalfromPopup() {
  loadingCircle.set(true);
    
  try {
    
    console.log("Checking:");
    console.log(selectedUser);
    console.log(user_id);
    
    await updateProfessional(selectedUser, user_id);
    await fetchUsers();
    await loadingCircle.set(false);
    console.log("set false");

    // Promise resolved successfully, show success alert
    const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = users.filter(user => user.professional_id === user_id);
      filteredUserArray.set(userArray);
      console.log(userArray);
    });
    unsubscribeUsers();
    
    alert('Professional updated successfully');
    
    closePopup(2);
  } catch (error) {
    loadingCircle.set(false);
    // Promise rejected or an error occurred, show error alert
    if (error.message === 'Professional not found') {
      alert('Professional not found');
    } else if (error.message === 'Internal server error') {
      alert('Internal server error');
    } else {
      alert('An unknown error occurred');
    }
  }
  
  
    
}
async function fetchFileHistory() {
    await fetchProfProgs();
    const unsubscribeProgs = profProgs.subscribe(progs => {
      ProgArray = progs.filter(prog => prog.professional_id === user_id);
      filteredProgArray.set(ProgArray);
    });
    unsubscribeProgs();
  }


  async function refresh(){
    
    
    loadingCircle.set(true);
        if(activeTab == 'users'){   
          await fetchUsers();

          // Promise resolved successfully, show success alert
          const unsubscribeUsers = allUsers.subscribe(users => {
            userArray = users.filter(user => user.professional_id === user_id);
            filteredUserArray.set(userArray);
            console.log(userArray);
          });
          unsubscribeUsers();
          
        }else if(activeTab == 'programs'){
          await fetchFileHistory();
        }
        loadingCircle.set(false);
     
  }


  async function deleteUser(user_id) {
  loadingCircle.set(true);
    
  try {
    
    console.log("Checking:");
    //console.log(selectedUser);
    //console.log(user_id);
    
    await updateProfessional(user_id, 0);
    await fetchUsers();
    await loadingCircle.set(false);
    //console.log("set false");

    // Promise resolved successfully, show success alert
    
    alert('Removed User Successfully');
    refresh();
    
    
  } catch (error) {
    loadingCircle.set(false);
    alert("Could not delete");
    console.log(error);
  }
  
  
    
}

async function deleteProgram(file_id) {
  loadingCircle.set(true);
    
  try {
    
    
    const role = "ROLE_PROFESSIONAL";
    //console.log(role);
    await deleteFile(file_id, role);
    await fetchUsers();
    await loadingCircle.set(false);
    //console.log("set false");

    // Promise resolved successfully, show success alert
    
    refresh();
    
    
  } catch (error) {
    loadingCircle.set(false);
    alert("Could not delete");
    console.log(error);
  }
   
}






    
</script>
    
    
    <div class="profile-window">
      <div class="user-details">
        <div class="user-info">
          <h3 class="profilename">{$altProfile.name} {$altProfile.surname}</h3>
          
          <p class="profile-role">{$altProfile.role}</p>
          <button class="detail-button" on:click={toggleDetails}><i class="fa-solid fa-user fa-lg" title="Details"></i><i class="fa-solid fa-bars fa-xs" title="Details"></i></button>
          {#if toggle}
            <div class="card" transition:slide>
              <div class="card-content">
                <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-envelope"></i>   </div>
                  <div class="info-value">{currentProfile.email}</div>
                </div>
                <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-cake-candles">   </i></div>
                  <div class="info-value">{currentProfile.date_of_birth}</div>
                </div>
                <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-square-phone"></i></div>
                  <div class="info-value">{currentProfile.phone_number}</div>
                </div>
                <div class="info-row">
                  <div class="info-label"><i class="fa-solid fa-location-dot"></i></div>
                  <div class="info-value">{currentProfile.address}</div>
                </div>
              </div>
            </div>
          {/if}
        </div>
        <div class = "avatar">
          {#if $altProfile.role === "Administrator"}
            <i class="fa-solid fa-user-shield fa-2xl"></i>
          {:else if $altProfile.role === "Professional" || $altProfile.role === "S. Professional"}
            <i class="fa-solid fa-user-tie fa-2xl"></i>
          {:else}
            <i class="fa-solid fa-user fa-2xl"></i>
          {/if}
        </div>
      </div>

      
    </div>
          <div class="lower-container">
    
            <div class="action-bar">
              <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
                {#if activeTab == 'users'}
              <button on:click={() => openPopup(2)}><i class="fa-solid fa-user-plus" title="add user"></i></button> 
              {:else}
                <button on:click={() => openPopup(3)}><i class="fa-solid fa-file-circle-plus" title="add existing file"></i></button>
                <button on:click={() => openPopup(1)}><i class="fa-solid fa-upload" title="upload new file"></i></button>
              {/if}
              <button on:click={refresh} class="add-user-button"><i class="fas fa-rotate" title="refresh"></i>
              </button>
            </div>
            <div class="tab-selector">
                <button class="{activeTab === 'users' ? 'active' : ''}" on:click={() => switchTab('users')}>Users</button>
                <button class="{activeTab === 'programs' ? 'active' : ''}" on:click={() => switchTab('programs')}>Programs</button>
            </div>
      
            {#if activeTab === 'users'}
            <div class="user-list-container">
              <div class="file-list">
                
                <table class="user-table">
                  <!-- Table header -->
                  <thead class="sticky-header">
                    <tr>
                      <th class="table-heading">Name</th>
                      <th class="table-heading">Email</th>
                      <th class="table-heading">Phone Number</th>
                      <th class="table-heading">Date of Birth</th>
                      <th class="table-heading"> </th>
                    </tr>
                  </thead>
            
                  <!-- Table body -->
                  <tbody>
                    {#each $filteredUserArray as user}
                    <tr class="clickable-row" >
                      <td class="table-entry"><button on:click={() => openUser(user.id)}>{user.name} {user.surname}</button></td>
                      <td class="table-entry">{user.email}</td>
                      <td class="table-entry">{user.phone_number}</td>
                      <td class="table-entry">{user.date_of_birth}</td>
                      <td class="table-entry"><button on:click={() => deleteUser(user.user_id)}><i class="fa-solid fa-trash delete-button"></i></button></td>
                    </tr>
                    {/each}
                  </tbody>
                </table>
              </div>
            </div>
            {/if}
      
            {#if activeTab === 'programs'}
            <div class="user-list-container">
              <div class="file-list">
                <table class="user-table">
                  <thead class="sticky-header">
                    <tr>
                      <th class="table-heading">Name</th>
                      <th class="table-heading">Created</th>
                      <th class="table-heading">Last Updated</th>
                      <th class="table-heading"> </th>
                    </tr>
                  </thead>
                  <tbody>
                    {#each $filteredProgArray as program}
                    <tr>
                      <td class="table-entry">{program.name}</td>
                      <td class="table-entry">{program.created_at}</td>
                      <td class="table-entry">{program.updated_at}</td>
                      <td class="table-entry"><button on:click={() => deleteProgram(program.id)}><i class="fa-solid fa-trash delete-button"></i></button></td>
                    </tr>
                    {/each}
                  </tbody>
                </table>
              </div>
            </div>
            
            {/if}
        </div>
   
    <div id="uploadPopup" class="popup">
      <div class="popup-content">
        <h2>Upload File</h2>
        <input type="file" id="fileInput">
        <input type="text" id="userIdInput" placeholder="User ID" readonly>
        <label>
          <input type="checkbox" id="updateCheckbox">
          Update existing file
        </label>
        <button on:click={uploadFileFromPopup}>Upload</button>
        <button on:click={()=> closePopup(1)}>Close</button>
      </div>
    </div>
    
    <div id="addProfPopup" class="popup2">
      <div class="popup-content">
        <h2>Add Professional</h2>
        <input type="text" placeholder="Search" bind:value={popupSearchTerm}>
        <label for="updateDropdown">Select a User:</label>
        <div class="popup-table">
          <table>
            <tbody>
              {#each $allUsers.filter(user => { return (user.name.toLowerCase().includes(popupSearchTerm) || user.surname.toLowerCase().includes(popupSearchTerm))}) as option}
                <tr
                  class:selected={selectedUser === option.user_id}
                  class:clickable-row={selectedUser !== option.user_id}
                  on:click={() => selectedUser = option.user_id}
                >
                  <td>{option.name} {option.surname}</td>
                </tr>
              {/each}
            </tbody>
          </table>
        </div>
        <button on:click={updateProfessionalfromPopup}>Add</button>
        <button on:click={() => closePopup(2)}>Close</button>
      </div>
    </div>

    <div id="addFilePopup" class="popup3">
      <div class="popup-content">
        <h2>Add Program</h2>
        <input type="text" placeholder="Search" bind:value={popupSearchTerm}>
        <label for="updateDropdown">Select a User:</label>
        <div class="popup-table">
          <table>
            <tbody>
              {#each $uniqueProgs.filter(program => { return (program.name.toLowerCase().includes(popupSearchTerm))}) as option}
                <tr
                  class:selected={selectedProgram.id === option.id}
                  class:clickable-row={selectedProgram.id !== option.id}
                  on:click={() => selectedProgram = option}
                >
                  <td>{option.name}</td>
                </tr>
              {/each}
            </tbody>
          </table>
        </div>
        <button on:click={console.log(selectedProgram)}>Add</button>
        <button on:click={() => closePopup(3)}>Close</button>
      </div>
    </div>


<style>
    .profile-window {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: start;
    }


    .user-details {
    display: flex;
    width: 100%;
    padding: 10px;
    flex-direction: row;
    align-items: start;
    justify-content: space-between;
    padding: 10px;
    margin-bottom: 10px;
  }

  .user-info {
    width: 60%;
  }

  .profilename {
    font-size: 24px;
    margin-bottom: 2px;
    margin-top: 12px;
  }

  .profile-role{
    font-size: 18px;
    color: #333;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  
  .avatar {
    height: 100%;
    padding-top: 30px;
    font-size: 48px;
    color: #00b6a0;
  }

  .detail-button {
    padding: 0;
    margin: 0;
    margin-bottom: 12px;
    background-color: transparent;
    color: #000000;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.detail-button:hover {
  color: #0056b3;
  background-color: transparent;
}


.card-content {
  display: flex;
  flex-direction: column;
}

.info-row {
  display: flex;
  margin-bottom: 8px;
}

.info-label {
  font-weight: bold;
  margin-right: 8px;
}

.info-value {
  flex-grow: 1;
}

.lower-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 0px;
    padding-bottom: 10px;
    justify-content: space-between;
    align-items: center;
    overflow-x: auto;
  }

  .action-bar {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0;
  padding-bottom: 12px;
}
.action-bar input[type="text"] {
  padding: 8px;
  flex: 1;
  margin-right: 30px;
  border: none; /* Remove the border */
  border-bottom: 1px solid #ccc; /* Add a bottom border */
  border-radius: 0; /* Remove border-radius */
  font-size: 14px;
}

.action-bar input[type="text"]:focus {
  outline: none;
  border-color: #999;
  box-shadow: none; /* Remove the box-shadow */
}
.action-bar button {
  padding: 0;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0;
  background-color: transparent;
  color: #000000;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
  transition: background-color 0.3s ease;
}

.action-bar button:hover{
  color: #0056b3;
  
}



  .tab-selector {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0;
  padding-bottom: 12px;
  padding-left: 0;
  padding-right: 0;
}


.tab-selector button {
  padding: 10px 20px;
  background-color: transparent;
  text-decoration: underline;
  margin: 0;
  color: #333;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
  width: 50%;
}



.tab-selector button:hover {
  background-color: #eeeeee;
}

.tab-selector button.active {
  background-color: #e6e6e6;
}


  .user-list-container{
    position: relative;
    height: 50vh;
    overflow-y: scroll;
    width: 100%;
  }

  
  

  

  /*.buttons {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .add-user-button {
    padding: 8px;
