  }

  .add-user-button:hover{
    background-color: #0056b3;
  }*/


  .user-table {
    table-layout: auto;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
    width: 100%;
    overflow-x: scroll;
}


.table-heading {
  text-align: center;
  white-space: nowrap;
  font-size: 15px;
  background-color: #ddd;
  
}

.table-entry {
    text-align: center;
    padding: 8px;
    border-bottom: 1px solid rgb(203, 203, 203);
    font-size: 14px;
    
  }

