  background-color: transparent;
  color: #000000;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

td button:hover{
  color: #0056b3;
  
}

.delete-button:hover {
  color: #e51e1e;
}
.clickable-row {
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .clickable-row:hover {
    background-color: lightgray;
  }

.sticky-header {
    position: sticky;
    top: 0;
    background-color: #f5f5f5;
  }

  .popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

